.nav-wrapper {
    max-width: 300px;
    height: 100vh;
    background-color: white;
    display: flex;
    justify-content: center;
    box-shadow: 4px 0 6px -2px rgba(0, 0, 0, 0.2);
    z-index: 1;

    nav {

        ul {

            li {
                list-style: none;
                
                a {
                    text-decoration: none;
                    color: #33424B;
                    font-size: 20px;
                    position: relative;
                    padding-bottom: 4px;
                    position: relative;

                    &.active-link {
                        

                        &::after {
                            content: '';
                            position: absolute;
                            bottom: 0;
                            left: 0;
                            width: 100%;
                            height: 3px;
                            background-color: #88C049;
                            transition: 3s;
                            transition: width 0.4s ease-in-out;
                        }
                        &.active-link::after {
                            width: 100%;
                        }
                    }
                }
            }
        }
    }
}